




























import {Vue, Component} from "vue-property-decorator";
import {trace_web} from "@/api";
import EditOpen from './EditOpen.vue'
import moment from "moment";
import MessageBox from "@/components/message-box/index.js";
import {ajaxDownload} from '@/util/apiUtil'

@Component({
    components: {
        EditOpen
    }
})
export default class Regulation extends Vue {
    $refs!: {
        editOpen: EditOpen
    }
    loading: boolean = false

    query: any = {
        pageNum: 1,
        pageSize: 10
    }

    list: any = {
        data: [],
        total: 0
    }

    columns: any[] = [
        {
            title: "文件名称",
            key: "name",
            minWidth: 200
        },
        {
            title: "上传时间",
            key: "updateTime",
            minWidth: 200,
            render: (h: Vue.CreateElement, params: any) => {
                return h('span', {
                    domProps: {
                        innerHTML: moment(params.updateTime).format('YYYY-MM-DD')
                    }
                })
            }
        },
        {
            title: "操作",
            key: 'option',
            minWidth: 100,
            fixed: 'right',
            render: (h: Vue.CreateElement, params: any) => {
                let btn = [];
                if (this.$SSO_AUC('editRegulation')) {
                    btn.push(
                        h('span', {
                            domProps: {
                                innerHTML: '编辑'
                            },
                            on: {
                                click: () => {
                                    this.$refs.editOpen.open(params.row);
                                }
                            }
                        })
                    )
                }
                if (this.$SSO_AUC('edlRegulation')) {
                    btn.push(
                        h('span', {
                            domProps: {
                                innerHTML: '删除'
                            },
                            class: 'red',
                            on: {
                                click: () => {
                                    this.delete(params.row)
                                }
                            }
                        })
                    )
                }
                if (this.$SSO_AUC('downloadFile')) {
                    btn.push(
                        h('span', {
                            domProps: {
                                innerHTML: '下载'
                            },
                            on: {
                                click: () => {
                                    this.downloadFile(params.row);
                                }
                            }
                        })
                    )
                }
                return h('div', {
                    class: 'handleBtn',
                }, btn)
            }
        }
    ]

    created() {
        this.queryList();
    }

    queryList(pageNum?: number) {
        this.query.pageNum = pageNum || 1;
        let params: any = {};
        for (let key in this.query) {
            if (this.query[key]) {
                params[key] = this.query[key]
            }
        }
        this.loading = true;
        trace_web.trace_web_policy_query_post({
            data: {
                ...params
            }
        }).then((res: any) => {
            this.list = res || {};
        }).finally(() => {
            this.loading = false;
        })
    }

    sizeChange(val: number) {
        this.query.pageSize = val;
    }

    add() {
        this.$refs.editOpen.open();
    }

    delete(row: any) {
        MessageBox.confirm(`您确定要删除“${row.name}”吗？删除后不能还原，请慎重考虑！`,
            '删除提醒！',
            {
                confirmButtonText: '确定删除',
                cancelButtonText: '取消',
                type: 'warning'
            }
        ).then(() => {
            trace_web.trace_web_policy_delete_post({
                data: {
                    id: row.id
                },
                load: {fullLoading: true}
            }).then(() => {
                this.$notify({
                    title: '成功',
                    message: '操作成功！',
                    type: 'success',
                });
                this.queryList();
            })
        }).catch(() => {
        })
    }

    downloadFile(row: any) {
        ajaxDownload('/file/download', {path: row.address, fileName: row.name + row.suffix});
    }
}
